* {
    margin: 0;
    padding: 0;
    font-family: Arial;
}

#root {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.App {
    width: 800px;
}

.post__btns {
    display: flex;
    gap: 4px;
}

button {
    width: 140px;
    padding: 8px 16px;
    background: #212197;
    border: 1px solid #fff;
    font-size: 14px;
    color: #fff;
    border-radius: 4px;
}

.post {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    border: 1px solid #212197;
    margin-top: 15px;
    border-radius: 2px;
}

.post-enter {
    opacity: 0;
}

.post-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}

.post-exit {
    opacity: 1;
}

.post-exit-active {
    opacity: 0;
    transition: opacity 500ms ease-in;
}

.pages__wrapper {
    display: flex;
    gap: 4px;
    margin: 30px 0;
}

.page {
    padding: 10px;
    border: 1px solid #212197;
}

.page__active {
    border: 2px solid #000000;
}

.navbar {
    width: 100vw;
    height: 45px;
    z-index: 2;
    background: #212197;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 15px;
}

.navbar__links {
    display: flex;
    justify-content: space-between;
    gap: 4px;
}

.navbar__links a {
    color: #ffffff;

}