.MyLoader {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    border: 3px dashed #212197;
    animation: rotate 1s infinite linear;
}

@keyframes rotate {
    from {
        transform: rotate(0deg) scale(0.9);
    }
    to {
        transform: rotate(360deg) scale(1);
    }
}