.myModal {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
    display: none;
    background: rgba(0,0,0,0.6);
}
.myModalContext {
    background: #ffffff;
    border-radius: 14px;
    padding: 14px;
    min-width: 250px;
}
.myModal.active {
    display: flex;
    align-items: center;
    justify-content: center;
}