.MyButton {
    margin: 15px 0;
    padding: 8px 16px;
    background-color: #212197;
    color: #fff;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 4px;
    font-size: 12px;
    transition: all .2s ease-in-out;
}

.MyButton:hover {
    transition: all .2s ease-in-out;
    transform: scale(1.01);
}